import React, { Component } from 'react';
import { Route } from 'react-router-dom';

import Home from '../Home/Home';
import About from '../About';
import Services from '../Services';
import Contact from '../Contact';
import SportsNews from '../SportsNews';
import FootballUpdates from '../FootballUpdates';
import CricketUpdates from '../CricketUpdates';

class RoutePaths extends Component {
    render() {
      return (            
            <div>
                <Route exact= {true} path="/" component={Home}/>
                <Route path="/About" component={About}/>
                <Route path="/Services" component={Services}/>
                <Route path="/Contact" component={Contact}/>
                <Route path="/SportsNews" component={SportsNews}/>
                <Route path="/FootballUpdates" component={FootballUpdates}/>
                <Route path="/CricketUpdates" component={CricketUpdates}/>
            </div>
        );
    }
}

export default RoutePaths;


